@use '../../scss/index' as *;

:root {
  --first-color: hsl(234, 62%, 56%);
  --icon-color: hsl(234, 12%, 24%);
  --body-color: hsl(234, 48%, 97%);
  --container-color: hsl(0, 0%, 14%);
}

$transition: 0.75s;
@keyframes move {
  0% {
    left: 10px;
  }
  100% {
    left: 80px;
  }
}
@keyframes cir {
  0% {
    transform: rotate(0deg) translateX(0px) rotate(-0deg);
  }
  100% {
    transform: rotate(120deg) translateX(70px) rotate(-120deg);
  }
}
@keyframes cir2 {
  0% {
    transform: rotate(0deg) translateX(0px) rotate(-0deg);
  }
  100% {
    transform: rotate(150deg) translateX(120px) rotate(-150deg);
  }
}

.header {
  height: 100px;
  margin: 0 2.5rem;
  padding: 0.5rem 0;
  @include flex(flex-start, center);
  @include mobile {
    @include flex(space-between, center);
    // flex-direction: column;
  }
  .header_logo {
    width: 100px;
    > img {
      width: 100%;
    }
  }
  .hamburger-bar {
    padding: 1rem 0;
    display: none;
    width: 40px;

    @include mobile {
      display: block;
    }
    .hamburger-bar_toggle {
      width: 40px;
      height: 5px;
      background-color: #fff;
      border-radius: 50px;
      position: relative;

      transition: width 0.5s ease;
      &::before {
        content: '';
        width: 40px;
        height: 5px;
        background-color: #fff;
        position: absolute;
        top: 10px;
        border-radius: 50px;
        transition: transform 0.5s ease;
      }
      &::after {
        content: '';
        width: 40px;
        height: 5px;
        background-color: #fff;
        position: absolute;
        top: -10px;
        border-radius: 50px;
        transition: transform 0.5s ease;
      }
      &.active {
        width: 0px;
        height: 5px;

        &::before {
          content: '';
          width: 40px;
          height: 5px;
          background-color: #fff;
          position: absolute;
          top: 0px;
          border-radius: 50px;
          transform: rotate(50deg);
        }
        &::after {
          content: '';
          width: 40px;
          height: 5px;
          background-color: #fff;
          position: absolute;
          top: 0px;
          border-radius: 50px;
          transform: rotate(-50deg);
        }
      }
    }
  }
  .nav-bar {
    width: 100%;
    @include flex(flex-start, center);
    @include mobile {
      flex-direction: column;
      @include flex(flex-start, center);
      z-index: 999;
      height: calc(100vh - 100px);
      background-color: #333;
      position: fixed;
      // left: 0;
      left: -1000px;
      top: 100px;
      width: 100%;
      transition: left 0.5s ease;
      &.active {
        left: 0;
      }
    }
    .headerLeft {
      @include flex(flex-start, center);
      width: 80%;
      @include mobile {
        @include flex(flex-start, center);
        flex-direction: column;
        margin: 2rem 0;
        width: 100%;
      }
      .header_icon {
        width: 80px;
        cursor: pointer;
      }
      .type_link {
        margin: 0 30px;
        font-size: 2rem;
        font-weight: 600;
        cursor: pointer;
        color: #fff;
        &:hover {
          color: #f3ce13;
        }
        @include table {
          font-size: 1.5rem;
          margin: 0 15px;
        }
        &.active {
          color: #f3ce13;
        }
        @include mobile {
          margin: 2rem 0;
        }
      }
    }
    .headerRight {
      width: 50%;
      @include flex(flex-end, unset);
      @include table {
        width: auto;
      }
      @include mobile {
        @include flex(flex-start, unset);
        margin-right: 0rem;
      }
      .headerRight_language {
        position: relative;
        .headerRight_toggle {
          cursor: pointer;
          height: 50px;
        }
        .headerRight_link {
          position: absolute;
          top: 0px;
          // left: 1780px;
          left: 10px;
          width: 30px;
          z-index: 10;
          .icon {
            width: 30px;
            z-index: -1;
          }
        }
        .btn_list {
          z-index: 1;

          .zh-TW {
            // position: relative;
            z-index: -1;
            transition: left ease 1s 1s, transform ease 1s, opacity ease 0.5s 1.2s, z-index ease 0.5s 1.2s;
            left: 10px;
            top: -7px;
            transform: rotate(0deg) translateX(0px) rotate(-0deg);
            opacity: 0;
          }
          .en {
            // position: relative;
            z-index: -1;
            transition: left ease 1s 1s, transform ease 1s, opacity ease 0.5s 1.2s, z-index ease 0.5s 1.2s;
            left: 10px;
            top: -7px;
            transform: rotate(0deg) translateX(0px) rotate(-0deg);
            opacity: 0;
          }
          &.active {
            .zh-TW {
              transition: left ease 1s, transform ease 1s 1s, opacity ease 0.5s;
              z-index: 10;
              left: 70px;
              transform: rotate(120deg) translateX(70px) rotate(-120deg);
              opacity: 1;

              .icon:hover {
                cursor: pointer;
              }
            }
            .en {
              transition: left ease 1s, transform ease 1s 1s, opacity ease 0.5s;
              z-index: 5;
              left: 70px;
              transform: rotate(150deg) translateX(120px) rotate(-150deg);
              opacity: 1;
              .icon:hover {
                cursor: pointer;
              }
            }
          }

          .icon {
            border-radius: 50%;
            color: #000;
            border: 5px solid #fff;
            width: 50px;
            height: 50px;
            text-shadow: 3px 3px 0px rgba(0, 0, 0, 0.2);
            box-shadow: 5px 4px 3px rgba(180, 173, 173, 0.96);
            font-size: 1.5rem;
            font-weight: 600;
            img {
              width: 100%;
            }
          }
        }
      }
    }
  }
}
a {
  @include flex(center, center);
}

